/* eslint-disable react-native/no-inline-styles */
import React, { lazy, Suspense } from "react";
import styled from "styled-components";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";

const MobileIntro = lazy(() =>
  import("./mobile/intro/mobileIntro").then((module) => ({
    default: module.MobileIntro,
  }))
);
const MobileDinoGlide = lazy(() =>
  import("./mobile/experience/dino glide/mobileDinoGlide").then((module) => ({
    default: module.MobileDinoGlide,
  }))
);
const MobileFiruco = lazy(() =>
  import("./mobile/experience/firuco/mobileFiruco").then((module) => ({
    default: module.MobileFiruco,
  }))
);
const MobileCru = lazy(() =>
  import("./mobile/experience/intern cru/mobileCru").then((module) => ({
    default: module.MobileCru,
  }))
);
const MobileUnc = lazy(() =>
  import("./mobile/education/mobileUnc").then((module) => ({
    default: module.MobileUnc,
  }))
);

const MobilePageStyler = styled.div`
  display: flex;
  flex-direction: column;
  overflow: hidden;
  transform-origin: top;
  background-color: lightyellow;
  width: 100vw;
`;

const TabBarWrapper = styled.div`
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: white;
`;

export const MobilePage = () => {
  const [activeTab, setActiveTab] = React.useState(0);

  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  return (
    <MobilePageStyler>
      <Suspense fallback={<div>Loading...</div>}>
        <TabBarWrapper>
          <Tabs
            value={activeTab}
            onChange={handleTabChange}
            scrollable
            variant="scrollable"
            style={{ overflow: "visible" }}
          >
            <Tab
              label="Intro"
              style={{
                width: "fit-content",
                padding: "2px",
              }}
            />
            <Tab
              label="Unc"
              style={{
                width: "fit-content",
                padding: "2px",
              }}
            />
            <Tab
              label="Dino Glide"
              style={{
                width: "fit-content",
                padding: "2px",
              }}
            />
            <Tab
              label="Firuco"
              style={{
                width: "fit-content",
                padding: "2px",
              }}
            />
            <Tab
              label="Cru"
              style={{
                width: "fit-content",
                padding: "2px",
                alignItems: "flex-start",
              }}
            />
          </Tabs>
        </TabBarWrapper>
        {activeTab === 0 && <MobileIntro />}
        {activeTab === 1 && <MobileUnc />}
        {activeTab === 2 && <MobileDinoGlide />}
        {activeTab === 3 && <MobileFiruco />}
        {activeTab === 4 && <MobileCru />}
      </Suspense>
    </MobilePageStyler>
  );
};
